.click-item {
    height: 170px;
    width: 170px;
    margin: 8px 25px;
    border: 1px solid #ff0000;
    border-radius: 3px;
    cursor: pointer;
    
}

.neutral {
    background-color: #ff0000;
}

.click-item:hover {
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05);
    -webkit-transition: .1s;
    -o-transition: .1s;
    transition: .1s;
}

.card-image {
    height: 100%;
    width: 100%;
}


